<template>
  <div class="bind-wrap">
    <app-header :title="dealerInfo.tel ? '解绑手机' : '绑定手机'" showBack="true"></app-header>
    <div class="bind-main">
      <mt-field label="手机号码" placeholder="请输入您的手机号码" v-model="tel">
        <mt-button type="default" size="small" @click="send">发送</mt-button>
      </mt-field>
      <mt-field label="验证码" placeholder="请输入您收到的验证码" v-model="code"></mt-field>
      <mt-button @click="bind" type="primary" size="large" style="margin: 2rem auto;">确认</mt-button>
      <mt-button @click="exit" type="primary" size="large" style="margin: 2rem auto;">退出</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import {bindTelForDealer, logoutDealer} from '../../api/maintain'
export default {
  name: 'Dealer-Bind-Tel',
  data () {
    return {
      tel: '',
      code: '',
      dealerInfo: ''
    }
  },
  created () {
    if (typeof this.$store.getters.getDealerInfo === 'string') {
      this.dealerInfo = JSON.parse(this.$store.getters.getDealerInfo)
    } else {
      this.dealerInfo = this.$store.getters.getDealerInfo
    }
  },
  methods: {
    send () {
      bindTelForDealer(null, this.tel)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg || '验证码发送成功')
          } else {
            this.$toast(data.errormsg || '验证码发送失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络繁忙，请重试!')
        })
    },
    bind () {
      if (!this.code) {
        return this.$toast('请输入您的验证码')
      }
      bindTelForDealer(this.code, this.tel, this.dealerInfo.tel ? 'true' : false)
        .then(data => {
          if (data.state) {
//            alert(data.errormsg)
            this.$toast(data.errormsg)
            this.$router.replace('/dealer')
          } else {
            this.$toast(data.errormsg)
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络繁忙，请重试')
        })
    },
    exit () {
      logoutDealer()
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg || '退出成功')
            this.$router.push('/login?type=dealer')
          } else {
            this.$toast(data.errormsg || '退出失败')
          }
        })
        .catch(err => {
          console.log((err))
          this.$toast('网络错误')
        })
    }
  },
  components: { AppHeader }
}
</script>

<style lang="less" scoped>
.bind-wrap{
  background: #e5e5e5;
  height: 100%;
  padding: 55px 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
</style>



